import React, { useEffect, useState } from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Row,
  Spinner,
} from 'reactstrap';
import { useQuery } from '@tanstack/react-query';
import { Link } from 'react-router-dom';
import { alert } from '../../components/toast/Toast';
import useAxios from '../../hooks/useAxios';

const Tenders = () => {
  const [isJoining, setIsJoining] = useState(false);
  const [availabelList, setAvailableList] = useState([]);
  const [registeredList, setRegisteredList] = useState([]);
  const [selectedId, setSelectedId] = useState();

  const api = useAxios();

  const { isLoading, error, data, refetch } = useQuery({
    queryKey: ['list-tender'],
    queryFn: () =>
      api.get(`vapi/tender/list-tender`).then((res) => {
        return res.data.data;
      }),
  });

  useEffect(() => {
    const availableFiltered = data?.filter((l) => {
      return l.register === false;
    });
    const registerFiltered = data?.filter((l) => {
      return l.register === true;
    });

    setAvailableList(availableFiltered);
    setRegisteredList(registerFiltered);
  }, [data]);

  const handleJoin = async (idTender) => {
    setIsJoining(true);
    await api
      .post('vapi/tender/register', { tender_id: idTender })
      .then(() => {
        refetch();
        alert('success', 'Berhasil mendaftar tender');
      })
      .catch(() => {
        alert('error', 'Something went wrong.');
      });
    setIsJoining(false);
  };

  return (
    <>
      <Row>
        <h4 className="mb-3 mt-3">Tender yang Diikuti</h4>
        {isLoading ? (
          <span>Loading..</span>
        ) : error ? (
          <span>Something went wrong.</span>
        ) : registeredList?.length > 0 ? (
          registeredList?.map((l) => (
            <Col md="6" lg="6" key={l.slug}>
              <Card className="border rounded-4 overflow-hidden">
                <CardHeader className="border-bottom fw-bold">{l.nama_tender}</CardHeader>
                <CardBody>
                  <table className="w-100">
                    <tbody>
                      <tr>
                        <td>Lokasi Pekerjaan</td>
                        <td>:</td>
                        <td>{l.lokasi}</td>
                      </tr>
                      <tr>
                        <td>Waktu Pendaftaran</td>
                        <td>:</td>
                        <td>
                          {l.tgl_pendaftaran} s/d {l.batas_pendaftaran}
                        </td>
                      </tr>
                      <tr>
                        <td>Jenis Pengadaan</td>
                        <td>:</td>
                        <td>{l.jenis_pengadaan}</td>
                      </tr>
                      <tr>
                        <td>HPS</td>
                        <td>:</td>
                        <td>Rp {l.hps}</td>
                      </tr>
                      <tr>
                        <td>Nomor KBLI</td>
                        <td>:</td>
                        <td>
                          {Array.isArray(JSON.parse(l.kbli))
                            ? JSON.parse(l.kbli).map((element, i) => {
                                return (
                                  <span key={element}>
                                    {element} {i + 1 === JSON.parse(l.kbli).length ? '' : '|'}{' '}
                                    &nbsp;
                                  </span>
                                );
                              })
                            : 'null'}
                        </td>
                      </tr>
                      <tr>
                        <td>Sistem Kualifikasi</td>
                        <td>:</td>
                        <td>{l.sistem_kualifikasi}</td>
                      </tr>
                    </tbody>
                  </table>
                  <div className="d-flex flex-column">
                    <div>
                      <Badge className="bg-light-primary text-primary p-2 mt-2 fw-bold">
                        {l.status_tender === 'buka' && ' Tender Dalam Tahap Pelaksanaan'}
                      </Badge>
                    </div>
                    <div>
                      <Badge className="bg-light-primary text-primary p-2 mt-2 fw-bold">
                        {l.status_peserta === 'lulus_tahap_1'
                          ? 'Anda Lulus Tahap I'
                          : l.status_peserta === 'pemenang'
                          ? 'Anda Terpilih Sebagai Penyedia'
                          : ''}
                      </Badge>
                    </div>
                  </div>
                </CardBody>
                <CardFooter className="border-top">
                  <div className="d-flex justify-content-end">
                    <div className="d-flex gap-2">
                      {l.status_tender === 'buka' && l.status_peserta === 'lulus_tahap_1' ? (
                        <Link to={`/tender/${l.slug}/upload-document?=tahap-dua`}>
                          <Button size="sm" type="button" outline className="rounded-3">
                            Upload Dokumen Tahap II
                          </Button>
                        </Link>
                      ) : l.status_tender === 'tutup' ? (
                        <Badge className="bg-light-danger text-danger p-2 fw-bold">
                          Tender Sudah Ditutup
                        </Badge>
                      ) : (
                        <Link to={`/tender/${l.slug}/upload-document`}>
                          <Button size="sm" type="button" outline className="rounded-3">
                            {l.sistem_kualifikasi === 'pasca kualifikasi'
                              ? 'Upload Dokumen'
                              : 'Upload Dokumen Tahap I'}
                          </Button>
                        </Link>
                      )}
                    </div>
                  </div>
                </CardFooter>
                {/* <CardFooter className="border-top">
                    <div className="d-flex justify-content-end">
                      <div className="d-flex gap-2">
                        <Button size="sm" type="button" outline className="rounded-3">
                          Input Dokumen Tahap II
                        </Button>
                        <Button size="sm" type="button" outline className="rounded-3">
                          Upload Dokumen Sanggah
                        </Button>
                        <Button size="sm" type="button" outline className="rounded-3">
                          Dokumen
                        </Button>
                      </div>
                    </div>
                  </CardFooter> */}
              </Card>
            </Col>
          ))
        ) : (
          <p className="ml-5">No data available.</p>
        )}
      </Row>
      <Row>
        <h4 className="mb-3 mt-3">Tender Tersedia</h4>
        {isLoading ? (
          <span>Loading..</span>
        ) : error ? (
          <span>Something went wrong.</span>
        ) : availabelList?.length > 0 ? (
          availabelList?.map((l) => (
            <Col md="6" lg="6" key={l.id_tender}>
              <Card className="border rounded-4 overflow-hidden">
                <CardHeader className="border-bottom fw-bold">{l.nama_tender}</CardHeader>
                <CardBody>
                  <table className="w-100">
                    <tbody>
                      <tr>
                        <td>Lokasi Pekerjaan</td>
                        <td>:</td>
                        <td>Pelabuhan Kuala Langsa</td>
                      </tr>
                      <tr>
                        <td>Waktu Pendaftaran</td>
                        <td>:</td>
                        <td>22-29 Oktober 2023</td>
                      </tr>
                      <tr>
                        <td>Jenis Pengadaan</td>
                        <td>:</td>
                        <td>Jasa</td>
                      </tr>
                      <tr>
                        <td>HPS</td>
                        <td>:</td>
                        <td>Rp {l.hps}</td>
                      </tr>
                      <tr>
                        <td>Nomor KBLI</td>
                        <td>:</td>
                        <td>
                          {Array.isArray(JSON.parse(l.kbli))
                            ? JSON.parse(l.kbli).map((element, i) => {
                                return (
                                  <span key={element}>
                                    {element} {i + 1 === JSON.parse(l.kbli).length ? '' : '|'}{' '}
                                    &nbsp;
                                  </span>
                                );
                              })
                            : 'null'}
                        </td>
                      </tr>
                      <tr>
                        <td>Sistem Kualifikasi</td>
                        <td>:</td>
                        <td>Pra Kualifikasi</td>
                      </tr>
                    </tbody>
                  </table>
                </CardBody>
                <CardFooter className="border-top">
                  <div className="d-flex justify-content-end">
                    <div className="d-flex gap-2">
                      <Link to={`/tender/${l.slug}`}>
                        <Button size="sm" type="button" outline className="rounded-3">
                          Lebih lanjut
                        </Button>
                      </Link>
                      {isJoining && l.id_tender === selectedId ? (
                        <Button type="button" color="success" size="sm">
                          <div className="d-flex align-items-center gap-2">
                            <Spinner size="sm" />
                            Mengirim..
                          </div>
                        </Button>
                      ) : (
                        <Button
                          type="button"
                          color="success"
                          className="rounded-3"
                          size="sm"
                          onClick={() => {
                            handleJoin(l.id_tender);
                            setSelectedId(l.id_tender);
                          }}
                        >
                          Daftar
                        </Button>
                      )}
                    </div>
                  </div>
                </CardFooter>
              </Card>
            </Col>
          ))
        ) : (
          <p className="ml-5">No data available.</p>
        )}
      </Row>
    </>
  );
};

export default Tenders;
